.App {
  text-align: center;
  padding: 3rem 1rem;
  max-width: 300px;
  width: 100%;
  margin: auto;
  box-sizing: border-box;
}

.image-box {
  padding-top: 2rem;
  text-align: center;
}

.image {
  width: 100%;
  display: block;  
}

.image-wrapper {
  border: 1px solid white;
}

.active {
  border: 1px solid red;
}